export default {
	data() {
		return {
			start:"2020-03-15",
			end:"2020-03-19",
			jobData:{}
		}
	},
	methods: {
		next(e){
			var _this = this
			uni.getStorage({
			    key: 'jobData',
			    success: function (res) {
			        console.log(res.data);
					_this.jobData = res.data
					_this.jobData.startTime = _this.start
					_this.jobData.endTime = _this.end
					console.log(_this.jobData);
					uni.setStorage({
					    key: 'jobData',
					    data: _this.jobData,
					    success: function () {　　　　　　　　　
					        // 存储成功后跳转第三步
					        uni.navigateTo({
					        	 url:"job-release-five/job-release-five"
					        })
					    }
					}) 
			    }
			});
		}
	}
}
